/*------------------------------------*\
	#EXPANSIBLE SECTIONS
\*------------------------------------*/
div.woocommerce .wfc-expansible-form-section .expansible-section__toggle-plus {
	position: relative;
	display: inline-block;
	width: auto;
	padding: 0 0 0 #{ $checkout-expansible-section-icon-size + $checkout-expansible-section-icon-spacing-left + $checkout-expansible-section-icon-spacing-right };
	cursor: pointer;
    text-decoration: $checkout-expansible-section-text-decoration;
	line-height: 1.2;

	&:before {
		@include icon( $checkout-expansible-section-icon );
		@include icon-size( $checkout-expansible-section-icon-size );
		
		position: absolute;
		left: $checkout-expansible-section-icon-spacing-left;
		top: 50%;
		transform: translateY( -50% );
	}

	&:focus {
		outline: $focus-outline-link;
		outline-offset: $focus-outline-offest-link;
	}
}





div.woocommerce .wfc-expansible-form-section.form-row .form-row {
	margin: 0;
	padding: 0;
	width: 100%;
}

div.woocommerce .wfc-expansible-form-section.form-row.form-row-last .wfc-expansible-form-section__toggle .collapsible-content__inner {
	@media (#{$bp-larger-than-tablet}) {
		padding-top: 1.8em;
	}
}





// Temporarily hide expansible form sections while the collapsible-block script is not yet loaded
body:not( .has-collapsible-block ) .wfc-expansible-form-section__toggle.is-collapsed .collapsible-content__inner,
body:not( .has-collapsible-block ) .wfc-expansible-form-section__content.is-collapsed .collapsible-content__inner {
	display: none;
}
